.drop_panel {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: white;
	z-index: 500;
	overflow-y: auto;
	padding: rem(30) rem(15);

	display: none;

	@include min540 {
		padding: rem(26) rem(15);
	}

	&__top {
		position: relative;
		margin-bottom: rem(50);

		display: flex;
		justify-content: center;

		.close {
			position: absolute;
			top: 0;
			left: 0;

			height: 18px;
			width: 18px;
			padding: 0;
			border: none;
			background: none;
			cursor: pointer;

			&:hover {
				svg {
					path {
						fill: var(--c-main);
					}
				}
			}
		}
	}

	&__bottom {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		.left {
			flex: 0 0 75%;
			max-width: 75%;

			display: flex;
			flex-wrap: wrap;

			@include min1050 {
				flex: 0 0 70%;
				max-width: 70%;
			}

			@include min {
				flex: 0 0 100%;
				max-width: 100%;
				margin-bottom: rem(20);
			}
		}

		.right {
			flex: 0 0 25%;
			max-width: 25%;

			border: 1px solid #F2F2F2;
			padding: 30px 35px 30px 35px;

			@include min1050 {
				flex: 0 0 30%;
				max-width: 30%;
			}

			@include min {
				flex: 0 0 100%;
				max-width: 100%;
			}

			a {
				transition: all 300ms ease;

				&:hover {
					color: var(--c-main) !important;
				}
			}

			.prv_top_number {
				color: #212121;
			}

			.footer-section__email a,
			.footer-section__network-list a {
				color: #212121;
			}
		}
	}

	&__itemlist {
		width: calc((100% - 100px) / 3);
		margin-right: rem(50);
		margin-bottom: rem(32);

		@include min1050 {
			width: calc((100% - 50px) / 2);
			margin-right: rem(25);
		}

		@include min768 {
			width: 100%;
			margin-right: 0;
		}

		&:nth-child(3n) {
			margin-right: 0;
		}

		&-arrow {
			width: rem(20);
			height: rem(20);
			border-radius: 50%;
			background-color: transparent;

			display: flex;
			align-items: center;
			justify-content: center;

			transition: all 200ms ease;

			i {
				color: #757575;
				padding-bottom: rem(5);
			}

			&:hover {
				background-color: var(--c-main);

				i {
					color: white;
				}
			}
		}

		&-arrow.active {
			transform: rotate(180deg);
		}

		> a {
			font-size: rem(19);
			font-weight: 600;

			transition: all 300ms ease;
			margin-bottom: rem(24);
			display: block;

			&:hover {
				color: var(--c-main);
			}
		}

		> ul {
			> li {
				display: flex;
				flex-wrap: wrap;
				justify-content: space-between;
				align-items: center;

				margin-bottom: rem(14);

				&:last-child {
					margin-bottom: 0;
				}

				> a {
					color: #757575;
					font-size: rem(14);
					transition: all 300ms ease;

					&:hover {
						color: var(--c-main);
					}
				}
			}
		}

		&-toggle {
			width: 100%;
			flex: 0 0 100%;
			padding-left: rem(16);

			margin-top: rem(8);

			display: none;

			> li {
				margin-bottom: rem(4);

				&:last-child {
					margin-bottom: 0;
				}

				> a {
					color: #757575;
					font-size: rem(14);
					transition: all 300ms ease;

					&:hover {
						color: var(--c-main);
					}
				}
			}
		}

		&-toggle.active {
			display: block;
		}
	}
}

.drop_panel.active {
	display: block;
}